<template>
  <div class="animated fadeIn">
    <b-card header-tag="header">
      <div slot="header">
        <b-row>
          <b-col>
            <i class="icon-drop"></i> This is Tpl
          </b-col>
          <b-col class="text-right">
            <b-dropdown text="View Source" class="m-0 p-0">
              <b-dropdown-item
                class="p-0"
                v-for="(x,index) in [
                `/src/components/ComTest2/${name}`,
                `/src/_nav.js`,
                '/src/store/Count.js',
              ]"
                :key="index"
              >
                <com-vs2 :file-path-link=" x "></com-vs2>
              </b-dropdown-item>
            </b-dropdown>
          </b-col>
        </b-row>
      </div>
      <b-card-body>
        <!-- User Interface controls -->
        <b-row>
          <b-col md="6" class="my-1">
            <b-form-group label-cols-sm="3" label="Filter" class="mb-0">
              <b-input-group>
                <b-form-input v-model="filter" placeholder="Type to Search"></b-form-input>
                <b-input-group-append>
                  <b-button :disabled="!filter" @click="filter = ''">Clear</b-button>
                </b-input-group-append>
              </b-input-group>
            </b-form-group>
          </b-col>

          <b-col md="6" class="my-1">
            <b-form-group label-cols-sm="3" label="Sort" class="mb-0">
              <b-input-group>
                <b-form-select v-model="sortBy" :options="sortOptions">
                  <option slot="first" :value="null">-- none --</option>
                </b-form-select>
                <b-form-select v-model="sortDesc" :disabled="!sortBy" slot="append">
                  <option :value="false">Asc</option>
                  <option :value="true">Desc</option>
                </b-form-select>
              </b-input-group>
            </b-form-group>
          </b-col>

          <b-col md="6" class="my-1">
            <b-form-group label-cols-sm="3" label="Sort direction" class="mb-0">
              <b-form-select v-model="sortDirection">
                <option value="asc">Asc</option>
                <option value="desc">Desc</option>
                <option value="last">Last</option>
              </b-form-select>
            </b-form-group>
          </b-col>

          <b-col md="6" class="my-1">
            <b-form-group label-cols-sm="3" label="Per page" class="mb-0">
              <b-form-select v-model="perPage" :options="pageOptions"></b-form-select>
            </b-form-group>
          </b-col>
        </b-row>

        <!-- Main table element -->
        <b-table size="sm"
          show-empty
          stacked="md"
          :items="items"
          :fields="fields"
          :current-page="currentPage"
          :per-page="perPage"
          :filter="filter"
          :sort-by.sync="sortBy"
          :sort-desc.sync="sortDesc"
          :sort-direction="sortDirection"
          @filtered="onFiltered"
        >
          <template slot="name" slot-scope="row">{{ row.value.first }} {{ row.value.last }}</template>

          <template slot="isActive" slot-scope="row">{{ row.value ? 'Yes :)' : 'No :(' }}</template>

          <template slot="cmd" slot-scope="row">
            <b-button
              size="sm"
              @click="info(row.item, row.index, $event.target)"
              class="mr-1"
            >Info modal</b-button>
            <b-button
              size="sm"
              @click="row.toggleDetails"
            >{{ row.detailsShowing ? 'Hide' : 'Show' }} Details</b-button>
          </template>

          <template slot="row-details" slot-scope="row">
            <b-card>
              <!-- <ul>
                <li v-for="(value, key) in row.item.ct" :key="key">{{ key }}: {{ value }}</li>
              </ul> -->
              <TableDetailHocSinh :lopid="row.item.id" :ds="row.item.ct"></TableDetailHocSinh>

            </b-card>
          </template>
        </b-table>

        <b-row>
          <b-col md="6" class="my-1">
            <b-pagination
              v-model="currentPage"
              :total-rows="totalRows"
              :per-page="perPage"
              class="my-0"
            ></b-pagination>
          </b-col>
        </b-row>

        <!-- Info modal -->
        <b-modal :id="infoModal.id" :title="infoModal.title" ok-only @hide="resetInfoModal">
          <pre>{{ infoModal.content }}</pre>
        </b-modal>
      </b-card-body>
    </b-card>
  </div>
</template>

<script>
//import ColorTheme from './ColorTheme'
import TableDetailHocSinh from '@/components/ComTest2/TableDetailHocSinh'

export default {
  name: "TableDetailLopHoc.vue",
  components: {
    //ColorTheme
    TableDetailHocSinh
  },

  computed: {
    sortOptions() {
      // Create an options list from our fields
      return this.fields
        .filter(f => f.sortable)
        .map(f => {
          return { text: f.label, value: f.key };
        });
    }
  },

  mounted() {
    // Set the initial number of items
    this.totalRows = this.items.length;
  },

  data() {
    return {
      name: `${this.$options.name}`,
      url: "",
      items: [
        { id: 1, ten: "Lớp 1", ct:[
          {id:1, ten:'thuc 101',diem:10},
          {id:2, ten:'thuc 102',diem:9},
          {id:3, ten:'thuc 103',diem:8},
        ] },
        { id: 2, ten: "Lớp 2", ct:[] },
        { id: 3, ten: "Lớp 3", ct:[] }
      ],
      fields: ["id", "ten", "cmd"],
      totalRows: 1,
      currentPage: 1,
      perPage: 5,
      pageOptions: [5, 10, 15],
      sortBy: null,
      sortDesc: false,
      sortDirection: "asc",
      filter: null,
      infoModal: {
        id: "info-modal",
        title: "",
        content: ""
      }
    };
  },
  created() {
    this.url = process.env.VUE_APP_API_URL;
    this.detailsAll();
  },
  methods: {
    detailsAll(){
      // for(var i=0;i<this.items.length;i++){
      //   this.items[i]['_showDetails']=true;
      // }
    },
    info(item, index, button) {
      this.infoModal.title = `Row index: ${index}`;
      this.infoModal.content = JSON.stringify(item, null, 2);
      this.$root.$emit("bv::show::modal", this.infoModal.id, button);
    },
    resetInfoModal() {
      this.infoModal.title = "";
      this.infoModal.content = "";
    },
    onFiltered(filteredItems) {
      // Trigger pagination to update the number of buttons/pages due to filtering
      this.totalRows = filteredItems.length;
      this.currentPage = 1;
    }
  }
};
</script>




